*, ::before, ::after {
    box-sizing: border-box;
    font-size: 62.5%;
}

html, body {
    color: white;
    background-color: #21232A;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 2rem;
}

button {
    font-size: 1.3rem;
    margin-right: 1rem;
    padding: .1rem;
}

input {
    color: grey;
    font-size: .5rem;
    width: 100%;
}